import React, {Component} from 'react';
import { TextField, List, ListItem, ListItemText } from "@material-ui/core";
import firebase from "./../firebase";
import $ from 'jquery';

export default class Bodycontenttv extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: [],
            count:0,
            channels: [],
            addsopcast: 'sop://broker.sopcast.com:3912/6002',
            addtitlesopcast: 'CCV5'
        };
    }

    componentDidMount() {
      //alert(localStorage.getItem('user_id'))
      this.getUser()
      this.getChannels()
    }

    getUser = () => {
      
      firebase.database().ref("/users").orderByChild('email')
      .equalTo(localStorage.getItem('user_id')).on('value', snapshot => {
        var userg = []
        snapshot.forEach(child => {
          var data = child.val()
        userg.push({
          id: data.key, 
          email: data.email,
          name: data.name,
          nickname: data.nickname,
          phone: data.phone,
          role: data.role,
          time: data.time
        })
        this.setState({ user: userg })
      })
        
      })
      setTimeout(function(){ 
      //alert(JSON.stringify(this.state.user[0].role))   
      }.bind(this), 2000);   
    }
    getChannels = () => {
      setTimeout(function(){ 
      if(this.state.user[0].role==='demo'){
        firebase.database().ref("/channels").orderByChild('id')
      .startAt('36').endAt('39').on('value', snapshot => {
        let newChannels = []
          snapshot.forEach(child => {
            var channel = child.val()
            newChannels.push({ id: channel.id, category: channel.category,channel_image: channel.channel_image,channel_title: channel.channel_title,channel_url: channel.channel_url })
          })
          this.setState({ channels: newChannels })
          //setTimeout(function(){
          //alert(JSON.stringify(this.state.channels))
          //}.bind(this), 2000);
          $('#divch').scrollTop($('#divch')[0].scrollHeight);        
      })
      }
        if(this.state.user[0].role==='admin' || this.state.user[0].role==='user'){
        //alert(this.state.messages)
        var messagesDB = firebase
          .database()
          .ref("/channels")
        messagesDB.on("value", snapshot => {
          let newChannels = []
          snapshot.forEach(child => {
            var channel = child.val()
            newChannels.push({ id: channel.id, category: channel.category,channel_image: channel.channel_image,channel_title: channel.channel_title,channel_url: channel.channel_url })
          })
          newChannels.splice(-1,1)
          this.setState({ channels: newChannels })
          //alert(JSON.stringify(this.state.channels))
          $('#divch').scrollTop($('#divch')[0].scrollHeight);
          //this.bottomSpan.scrollIntoView({ behavior: "smooth" })
        })
        }

      }.bind(this), 3500); 
      }

      setsopcast = (address,title) => {
        this.setState({ addsopcast: address, addtitlesopcast: title })
      }

      renderChannels = () => {
        //if(this.state.count < this.state.channels.length){
        return this.state.channels.map(channel => (
            <tr><td align="right">{channel.id}</td><td align="right"><img height="30" width="70" src={`data:image/png;base64,${channel.channel_image}`}/></td><td align="center"><a className="btn btn-warning" href={channel.channel_url}>{channel.channel_title}</a></td><td><button className="btn btn-warning" onClick={() => this.setsopcast(channel.channel_url,channel.channel_title)}>Play in Browser</button></td></tr>
        ));
        //this.setState({ count: this.state.count+1 });
        //}
      }

    render(){
        const divStyle={
            overflowY: 'scroll',
            border:'1px solid red',
            float: 'left',
            height:'500px',
            position:'relative'
          };
          const divStyless={  
            height:'150px',
          }
        return (
            <main role="main" className="col-md-9 ml-sm-auto col-lg-10 pt-3 px-4">
            <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pb-2 mb-3 border-bottom">
              <h1 className="h2">Dashboard TV Page</h1>
            </div>
            <p>Dashboard TV Page</p>

            <div id="divch" style={divStyle}><table>{this.renderChannels()}</table></div>
            <object 
                id="SopPlayer" name = "SopPlayer" 
                classID="clsid:8FEFF364-6A5F-4966-A917-A3AC28411659" 
                codebase="http://download.sopcast.com/download/SOPCORE.CAB#version=1,1,2,0" 
                height="350" width="450">
                <param name="AutoStart" value="1" />  
                <param name="SopAddress" value={this.state.addsopcast} />
                <param name="ChannelName" value={this.state.addtitlesopcast} />
            </object>
            <div style={divStyless}></div>
          </main>  
        )
    }
}